.snip15151 {
  font-family: 'Open Sans', Arial, sans-serif;
  position: relative;
  margin: 10px;
  min-width: 230px;
  max-width: 315px;
  width: 100%;
  color: #000000;
  text-align: center;
  line-height: 1.4em;
  font-size: 14px;
  box-shadow: none !important;
}

.snip15151 * {
  box-sizing: border-box;
}

.snip15151 .profile-image {
  display: inline-block;
  width: 80%;
  z-index: 1;
  position: relative;
  padding: 10px;
  border: 2px solid #50E3C2;
}

.snip15151 .profile-image img {
  max-width: 100%;
  vertical-align: top;
}

.snip15151 figcaption {
  width: 100%;
  background-color: #F2F2F2;
  color: #555;
  padding: 125px 25px 25px;
  margin-top: -100px;
  display: inline-block;
}

.snip15151 h3,
.snip15151 h4,
.snip15151 p {
  margin: 0 0 5px;
}

.snip15151 h3 {
  font-weight: 600;
  font-size: 1.3em;
  font-family: 'Playfair Display', Arial, sans-serif;
}

.snip15151 h4 {
  color: #8c8c8c;
  font-weight: 400;
  letter-spacing: 2px;
}

.snip15151 p {
  font-size: 0.9em;
  letter-spacing: 1px;
  opacity: 0.9;
}

.snip15151 .icons {
  text-align: center;
  width: 100%;
}

.snip15151 i {
  padding: 10px 2px;
  display: inline-block;
  font-size: 18px;
  font-weight: normal;
  color:#50E3C2;
  opacity: 0.75;
}

.snip15151 i:hover {
  opacity: 1;
  transition: all 0.35s ease;
}

