.card {
background-color: black;

}


  section, article {
	background: inherit;
	overflow: hidden;
	position: relative;
	text-align: center;
	width: auto;
	width: initial;
  }
  article img {
	display: block;
	width: 100%
  }
  article:nth-child(2n) {
transform: rotate3d(0,1,0,180deg);
  }
  article:nth-child(2n) .box .feed {
transform: rotate3d(0,1,0,180deg);
  }
  article .box:before {
	background: #FFF;
	content: "";
	margin-top: -10px;
	position: absolute;
	width: 20px;
	height: 20px;
	top: 50%;
	left: -10px;
transform: rotate(45deg);
  }
  .box .feed {
	height: 100%;
	overflow: hidden;
	padding: 0 10px;
	position: relative;
	width: auto;
	width: initial;
  }
  .box .feed h2 {
	color: #FF5B06;
	font-weight: 400;
	letter-spacing: 1px;
  }
  .box .feed p {
	color: #555;
	letter-spacing: 1px;
	line-height: 22px;
  }

  
  
